@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{background-color:#f6f7f8;font-size:100%;box-sizing:border-box}body{margin:0;padding:0;height:100vh;font-family:"Open Sans", Arial, Helvetica, sans-serif}*,*::before,*::after{box-sizing:inherit}.App{display:flex;flex-direction:column;text-align:center;min-height:100vh}.container{flex:1 1}.App-logo{height:40vmin;pointer-events:none}.App-header{background-color:#fafafa;border:black;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:black}.container{min-height:calc(100vh - 85px);margin:1rem;display:flex;flex-wrap:wrap}.container .content{width:70rem;margin:0 auto;text-align:left}.container .content>div:not(:first-of-type){margin-top:5rem}.container .content>div:last-of-type{margin-bottom:5rem}.container h1{display:inline-block;text-align:left;margin:0 0 2rem 0;padding:0.5rem 0 0}

ul{list-style-type:none;margin:0;padding:0}li{display:inline}a{display:block;text-decoration:none}ul li>a{padding:8px;color:#fff}nav.navbar{background-color:#303846;display:flex;justify-content:space-between;align-items:center;overflow:hidden;top:0;height:50px;padding:0 1rem}nav.navbar .navbar__container{width:70rem;display:flex;justify-content:space-between;flex-wrap:wrap}nav.navbar .navbar-name{font-size:24px;font-weight:700;letter-spacing:3px;color:#fff}nav.navbar .navbar-name:hover{color:#10b9b3}nav.navbar .navbar-name__nickname{color:#10b9b3}nav.navbar ul{display:flex;align-items:center;justify-content:flex-end}nav.navbar ul li{margin-right:20px}nav.navbar ul li:last-child{margin-right:0px}nav.navbar ul li a:hover,nav.navbar ul li a:active{color:#10b9b3}nav.navbar ul li a{font-size:14px;font-weight:bold}@media only screen and (max-width: 320px){nav.navbar .navbar-name{font-size:24px}}@media only screen and (max-width: 600px){nav.navbar{flex-wrap:wrap;height:110px;padding:1rem;grid-row-gap:16px;row-gap:16px;border-bottom:1px solid #80808012}nav.navbar ul li{width:30vw;margin-right:0}nav.navbar .navbar-name{font-size:24px;letter-spacing:2px}nav.navbar ul{display:flex;flex-basis:100%;align-items:center;justify-content:center}}@media only screen and (min-width: 768px){nav.navbar{display:flex;flex-wrap:wrap;justify-content:center;align-items:center}}

.billboard{background-color:#303846;height:22rem;display:flex;align-items:center;justify-content:center}.billboard .billboard-text{color:white;margin:0 6vw}.billboard h1{text-align:center;color:white;margin:0 0 0.5rem 0;font-size:48px}.billboard h5{margin:0}.billboard .billboard-text__nickname{background-color:#14b9af;color:#fff;padding:0 12px}.billboard .billboard-role{color:#10b9b3}.billboard .billboard-image{align-items:center;margin-top:23rem}.billboard .billboard-image img{border-radius:50%;-webkit-user-select:none;-ms-user-select:none;user-select:none;pointer-events:none}@media only screen and (max-width: 768px){.billboard-text>h1{font-size:32px}.fa-2x{font-size:1.5em}}@media only screen and (max-width: 1440px){.billboard-image img{display:none}}

ul.content-technologies{display:grid;grid-template-columns:repeat(2, minmax(140px, 200px));list-style-type:square;margin:0 0 2rem 2rem;grid-gap:0 40px;gap:0 40px}ul.content-technologies li{display:list-item}.content--about__paragraph{width:100%}.downloadCV--button{background-color:#303846;color:#fff;border:1px solid #303846;padding:8px;width:10rem;border-radius:16px;text-align:center}.downloadCV--button:hover{background:#10b9b3;border:1px solid #10b9b3}@media only screen and (max-width: 375px){ul.content-technologies{display:block}}@media only screen and (min-width: 1440px){.container .content .content--about__paragraph{width:40rem;line-height:1.5}}

hr{border:0}.projects--container{display:flex;flex-wrap:wrap;text-align:left;align-items:center}.projects--content{width:349px;height:100%;background-color:#fff;border-radius:18px;box-shadow:0 3px 10px grey;margin:12px}.projects--content .project--content__imageAnchor{color:#303846}.projects--content .project--content__imageAnchor:active,.projects--content .project--content__imageAnchor:visited{color:#303846}.projects--content .projects--button{position:absolute;margin-top:-15rem;z-index:2;display:none}.projects--content .projects--content:hover+.projects--button{display:block}.projects__content--technologies{font-size:12px;color:#303846ad;margin:0}.content--projects__description{padding:0 1rem}.content--projects__description:active{color:#303846ad}.content--projects__description>h4{margin:0 0 0rem}.content--projects__description>p{color:#222;opacity:.6}.projects--container>a{flex-basis:50%}.content--projects__img{width:100%;height:100%;border-top-left-radius:18px;border-top-right-radius:18px}.content--projects__img:active{color:#303846}.helm{display:flex;justify-content:center;margin:1rem 1rem 1.5rem}.helm>a button{background-color:#303846;border:none;color:#fff;font-size:1rem;font-weight:500;border-radius:16px;width:12rem;height:2.5rem}.helm>a button:hover{background-color:#10b9b3;cursor:pointer}@media screen and (max-width: 768px){.projects--content{flex-grow:1;width:100%;margin:12px 0}.content--projects__img{width:100%}}@media screen and (max-width: 1024px){.projects--content{flex-grow:1}}

.content-contact a{display:inline-block;font-size:large;font-weight:600;color:#303846;text-decoration:underline}.contact-social{display:inline-flex}.contact-social li a{display:inline-block;color:#303846}.contact-social li a:hover{color:#10b9b3}

footer.footer{background-color:#303846;color:white;position:relative;bottom:0;width:100%;height:35px;display:flex}.footer-text__copyCurrentYear{color:#10b9b3}.footer-text{font-size:12px;margin:auto}.footer-github__link{display:inline;color:#fff}

